import { actionCreator } from '../../utils/generators';
import LocalisationsActionTypes from './localisations.types';

export const getGabLocalisations = () => ({
  type: actionCreator('req', LocalisationsActionTypes.GET_GAB_LOCALISATIONS),
});
export const getBranchesLocalisations = () => ({
  type: actionCreator(
    'req',
    LocalisationsActionTypes.GET_BRANCHES_LOCALISATIONS,
  ),
});
export const setBranchFiltredLocations = filtered => ({
  type: actionCreator(
    'req',
    LocalisationsActionTypes.SET_BRANCH_FILTERED_LOCATIONS,
  ),
  payload: filtered,
});
export const setGabFiltredLocations = filtered => ({
  type: actionCreator(
    'req',
    LocalisationsActionTypes.SET_GAB_FILTERED_LOCATIONS,
  ),
  payload: filtered,
});
export const setSelectedMark = Mark => ({
  type: actionCreator('req', LocalisationsActionTypes.SET_SELECTED_MARK),
  payload: Mark,
});
export const setHoveredMark = Mark => ({
  type: actionCreator('req', LocalisationsActionTypes.SET_HOVERED_MARK),
  payload: Mark,
});
export const setFitBoundFlag = boolValue => ({
  type: actionCreator('req', LocalisationsActionTypes.SET_BOUND_FLAG),
  payload: boolValue,
});
export const setMapZoom = number => ({
  type: actionCreator('req', LocalisationsActionTypes.SET_MAP_ZOOM),
  payload: number,
});
export const setActiveTab = tab => ({
  type: actionCreator('req', LocalisationsActionTypes.SET_ACTIVE_TAB),
  payload: tab,
});
