import React from 'react';
import { Dropdown } from 'primereact/dropdown';
import { Formik } from 'formik';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import {
  setBranchFiltredLocations,
  setSelectedMark,
  setHoveredMark,
  setFitBoundFlag,
  setMapZoom,
} from '../../../redux/geoLocalisation/localisations.actions';
import ResetIcon from '../../../assets/SvgComponents/ResetSvg/resetSvg';
import { t } from '../../../i18n';
import { GEOLOCALISATION_NAMESPACE } from '../constants';

const BranchTab = props => {
  let data = [];
  return (
    <Formik
      initialValues={{
        chosenSubsidiary: '',
        openSaturday: false,
      }}
      onSubmit={(values, { setSubmitting }) => {
        setSubmitting(false);
      }}
    >
      {({ values, handleChange, handleBlur, setFieldValue }) => {
        if (values.openSaturday) {
          data = props.branchesInfo.filter(
            branch =>
              branch.bank === values.chosenSubsidiary &&
              branch.isOpenSaturday === 1,
          );
        } else {
          data = props.branchesInfo.filter(
            branch => branch.bank === values.chosenSubsidiary,
          );
        }
        props.setBranchFiltredLocations(data);
        return (
          <div className="tab-pane cont active">
            <form>
              <div className="form-group">
                <label htmlFor="">
                  {t(`${GEOLOCALISATION_NAMESPACE}bank`)}
                </label>
                <Dropdown
                  name="chosenSubsidiary"
                  options={props.options}
                  value={values.chosenSubsidiary}
                  onChange={e => {
                    setFieldValue('chosenSubsidiary', e.value);
                    props.setSelectedMark(null);
                    props.setFitBoundFlag(true);
                    props.setMapZoom(10);
                  }}
                  placeholder={t(`${GEOLOCALISATION_NAMESPACE}chooseBank`)}
                />
              </div>
              <div className="form-group">
                <div className="bcp-checkbox">
                  <input
                    name="openSaturday"
                    id="remember"
                    type="checkbox"
                    onChange={e => {
                      handleChange(e);
                      props.setSelectedMark(null);
                      props.setFitBoundFlag(true);
                      props.setMapZoom(10);
                    }}
                    onBlur={handleBlur}
                    value={values.openSaturday}
                    checked={values.openSaturday}
                  />
                  <label htmlFor="remember">
                    {t(`${GEOLOCALISATION_NAMESPACE}openSaturday`)}
                  </label>
                </div>
              </div>
              <div
                onClick={() => {
                  setFieldValue('chosenSubsidiary', '');
                  setFieldValue('openSaturday', false);
                  props.setSelectedMark(null);
                  props.setFitBoundFlag(true);
                  props.setMapZoom(10);
                }}
                className="form-group effacer-recherche"
              >
                <div className="icon">
                  <ResetIcon />
                </div>
                <span> {t(`${GEOLOCALISATION_NAMESPACE}reset`)}</span>
              </div>
            </form>
            <div className="list-agences">
              <h3>{t(`${GEOLOCALISATION_NAMESPACE}agencyList`)}</h3>
              <div className="scrollbar">
                <div className="list-content">
                  {props.branchesInfo.map(branch => {
                    let ref;
                    const handleClick = () =>
                      ref.current.scrollIntoView({
                        behavior: 'smooth',
                        block: 'end',
                        inline: 'start',
                      });
                    if (values.openSaturday) {
                      if (
                        branch.bank === values.chosenSubsidiary &&
                        branch.isOpenSaturday === 1
                      ) {
                        ref = React.createRef();
                        return (
                          <div
                            key={branch.branchId}
                            ref={ref}
                            className={
                              props.selectedMark === branch
                                ? 'list-el active-item'
                                : 'list-el'
                            }
                            onClick={() => {
                              handleClick();
                              props.setSelectedMark(branch);
                              props.setFitBoundFlag(false);
                              props.setMapZoom(19);
                            }}
                            onMouseOver={() => {
                              props.setHoveredMark(branch.branchId);
                            }}
                            onFocus={() => {
                              props.setHoveredMark(branch.branchId);
                            }}
                            onMouseOut={() => {
                              props.setHoveredMark(null);
                            }}
                            onBlur={() => {
                              props.setHoveredMark(null);
                            }}
                          >
                            <div className="name">
                              {t(`${GEOLOCALISATION_NAMESPACE}agency`)} -{' '}
                              {branch.name}
                            </div>
                            <div className="adresse">{branch.address} </div>
                            <div className="telephone">{branch.tel}</div>
                            <div className="hour">
                              {branch.horaire}
                              {t(`${GEOLOCALISATION_NAMESPACE}openSaturday`)}
                            </div>
                            <div className="link">
                              {t(`${GEOLOCALISATION_NAMESPACE}knowMore`)}
                            </div>
                          </div>
                        );
                      } else {
                        return null;
                      }
                    } else {
                      if (branch.bank === values.chosenSubsidiary) {
                        ref = React.createRef();
                        return (
                          <div
                            key={branch.branchId}
                            ref={ref}
                            className={
                              props.selectedMark === branch
                                ? 'list-el active-item'
                                : 'list-el'
                            }
                            onClick={() => {
                              handleClick();
                              props.setSelectedMark(branch);
                              props.setFitBoundFlag(false);
                              props.setMapZoom(19);
                            }}
                            onMouseOver={() => {
                              props.setHoveredMark(branch.branchId);
                            }}
                            onFocus={() => {
                              props.setHoveredMark(branch.branchId);
                            }}
                            onMouseOut={() => {
                              props.setHoveredMark(null);
                            }}
                            onBlur={() => {
                              props.setHoveredMark(null);
                            }}
                          >
                            <div className="name">
                              {t(`${GEOLOCALISATION_NAMESPACE}agency`)} -{' '}
                              {branch.name}
                            </div>
                            <div className="adresse">{branch.address} </div>
                            <div className="telephone">{branch.tel}</div>
                            <div className="hour">
                              {branch.horaire}
                              {'  '}
                              {branch.isOpenSaturday === 1
                                ? t(`${GEOLOCALISATION_NAMESPACE}openSaturday`)
                                : '-'}
                            </div>
                            <div className="link">
                              {t(`${GEOLOCALISATION_NAMESPACE}knowMore`)}
                            </div>
                          </div>
                        );
                      } else {
                        return null;
                      }
                    }
                  })}
                </div>
              </div>
            </div>
          </div>
        );
      }}
    </Formik>
  );
};
const mapDispatchToProps = dispatch => ({
  setBranchFiltredLocations: data => {
    dispatch(setBranchFiltredLocations(data));
  },
  setSelectedMark: mark => {
    dispatch(setSelectedMark(mark));
  },
  setHoveredMark: mark => {
    dispatch(setHoveredMark(mark));
  },
  setFitBoundFlag: value => {
    dispatch(setFitBoundFlag(value));
  },
  setMapZoom: value => {
    dispatch(setMapZoom(value));
  },
});
const mapStateToProps = state => ({
  branchesInfo: state.GeoLocalisations.branchesInfo,
  selectedMark: state.GeoLocalisations.selectedMark,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(BranchTab),
);
