import React from 'react';
import { withTranslation } from 'react-i18next';
import { connect } from 'react-redux';
import {
  GoogleMap,
  withScriptjs,
  withGoogleMap,
  Marker,
  InfoWindow,
  BicyclingLayer,
} from 'react-google-maps';
import { t } from '../../../i18n';
import { GEOLOCALISATION_NAMESPACE } from '../constants';
import {
  setSelectedMark,
  setHoveredMark,
  setFitBoundFlag,
  setMapZoom,
} from '../../../redux/geoLocalisation/localisations.actions';
const styles = require('../styles/GoogleMapStyles.json');
let map;
let bounds;
class Map extends React.Component {
  componentDidUpdate(prevProps) {
    if (this.props.fitBoundFlag !== prevProps.fitBoundFlag) {
      if (this.props.fitBoundFlag) {
        map.fitBounds(bounds);
      }
    }
  }
  render() {
    bounds = new window.google.maps.LatLngBounds();
    const data =
      this.props.activeTab === 'gab'
        ? this.props.gabFilteredLocations.length < 1
          ? this.props.gabsInfo
          : this.props.gabFilteredLocations
        : this.props.branchFilteredLocations.length < 1
        ? this.props.branchesInfo
        : this.props.branchFilteredLocations;
    data.map(element => {
      const latitude = element.latitude;
      const longitude = element.longitude;
      const latLng = new window.google.maps.LatLng(latitude, longitude);
      bounds.extend(latLng);
    });
    return (
      <GoogleMap
        ref={ref => {
          map = ref;
        }}
        defaultZoom={this.props.mapZoom}
        zoom={this.props.mapZoom}
        defaultClickableIcons
        clickableIcons
        defaultOptions={{
          disableDefaultUI: true, // disable default map UI
          draggable: true, // make map draggable
          keyboardShortcuts: false, // disable keyboard shortcuts
          scaleControl: true, // allow scale controle
          scrollwheel: true, // allow scroll wheel
          styles: styles, // change default map styles
        }}
        onClick={() => {
          this.props.setSelectedMark(null);
        }}
        onIdle={() => {
          if (this.props.fitBoundFlag) {
            map.fitBounds(bounds);
          }
          if (this.props.selectedMark) {
            const latitude = this.props.selectedMark.latitude;
            const longitude = this.props.selectedMark.longitude;
            const latLng = new window.google.maps.LatLng(latitude, longitude);
            map.panTo(latLng);
            this.props.setFitBoundFlag(false);
          }
        }}
      >
        <BicyclingLayer autoUpdate />
        {this.props.activeTab === 'branch'
          ? data.map(branch =>
              this.props.hoveredMark === branch.branchId ? (
                <Marker
                  animation={1}
                  key={branch.branchId}
                  position={{
                    lat: branch.latitude,
                    lng: branch.longitude,
                  }}
                  onClick={() => {
                    this.props.setSelectedMark(branch);
                  }}
                  onMouseOver={() => {
                    this.props.setHoveredMark(branch.branchId);
                  }}
                  onFocus={() => {
                    this.props.setHoveredMark(branch.branchId);
                  }}
                  onMouseOut={() => {
                    this.props.setHoveredMark(null);
                  }}
                  onBlur={() => {
                    this.props.setHoveredMark(null);
                  }}
                  icon={{
                    url: require('../../../assets/img/mapIcons/marker-map.png'),
                    scaledSize: new window.google.maps.Size(48, 60),
                  }}
                />
              ) : (
                <Marker
                  animation={4}
                  key={branch.branchId}
                  position={{
                    lat: branch.latitude,
                    lng: branch.longitude,
                  }}
                  onClick={() => {
                    map.panTo(
                      new window.google.maps.LatLng(
                        branch.latitude,
                        branch.longitude,
                      ),
                    );
                    this.props.setSelectedMark(branch);
                  }}
                  onMouseOver={() => {
                    this.props.setHoveredMark(branch.branchId);
                  }}
                  onFocus={() => {
                    this.props.setHoveredMark(branch.branchId);
                  }}
                  onMouseOut={() => {
                    this.props.setHoveredMark(null);
                  }}
                  onBlur={() => {
                    this.props.setHoveredMark(null);
                  }}
                  icon={{
                    url: require('../../../assets/img/mapIcons/marker-map.png'),
                    scaledSize: new window.google.maps.Size(40, 50),
                  }}
                />
              ),
            )
          : data.map(gab =>
              this.props.hoveredMark === gab.gabId ? (
                <Marker
                  animation={1}
                  key={gab.gabId}
                  position={{
                    lat: gab.latitude,
                    lng: gab.longitude,
                  }}
                  onClick={() => {
                    map.panTo(
                      new window.google.maps.LatLng(
                        gab.latitude,
                        gab.longitude,
                      ),
                    );
                    this.props.setSelectedMark(gab);
                  }}
                  onMouseOver={() => {
                    this.props.setHoveredMark(gab.gabId);
                  }}
                  onFocus={() => {
                    this.props.setHoveredMark(gab.gabId);
                  }}
                  onMouseOut={() => {
                    this.props.setHoveredMark(null);
                  }}
                  onBlur={() => {
                    this.props.setHoveredMark(null);
                  }}
                  icon={{
                    url: require('../../../assets/img/mapIcons/marker-gab.png'),
                    scaledSize: new window.google.maps.Size(48, 63),
                  }}
                />
              ) : (
                <Marker
                  animation={4}
                  key={gab.gabId}
                  position={{
                    lat: gab.latitude,
                    lng: gab.longitude,
                  }}
                  onClick={() => {
                    map.panTo(
                      new window.google.maps.LatLng(
                        gab.latitude,
                        gab.longitude,
                      ),
                    );
                    this.props.setSelectedMark(gab);
                  }}
                  onMouseOver={() => {
                    this.props.setHoveredMark(gab.gabId);
                  }}
                  onFocus={() => {
                    this.props.setHoveredMark(gab.gabId);
                  }}
                  onMouseOut={() => {
                    this.props.setHoveredMark(null);
                  }}
                  onBlur={() => {
                    this.props.setHoveredMark(null);
                  }}
                  icon={{
                    url: require('../../../assets/img/mapIcons/marker-gab.png'),
                    scaledSize: new window.google.maps.Size(40, 53),
                  }}
                />
              ),
            )}

        {this.props.selectedMark && (
          <InfoWindow
            position={{
              lat: this.props.selectedMark.latitude,
              lng: this.props.selectedMark.longitude,
            }}
            onCloseClick={() => {
              this.props.setSelectedMark(null);
            }}
          >
            <div className="popin-map">
              <h2>{this.props.selectedMark.name}</h2>
              <p className="adress">
                {t(`${GEOLOCALISATION_NAMESPACE}address`)}:
                {this.props.selectedMark.address}
              </p>
              {this.props.activeTab === 'branch' ? (
                <>
                  <p className="fax">
                    {t(`${GEOLOCALISATION_NAMESPACE}fax`)}:
                    {this.props.selectedMark.fax}
                  </p>
                  <p className="tel">
                    {t(`${GEOLOCALISATION_NAMESPACE}phone`)}:
                    {this.props.selectedMark.tel}
                  </p>
                  <p className="hour">
                    {t(`${GEOLOCALISATION_NAMESPACE}schedule`)}:
                    {this.props.selectedMark.horaire}
                  </p>
                </>
              ) : null}
            </div>
          </InfoWindow>
        )}
      </GoogleMap>
    );
  }
}

const WrappedMap = withScriptjs(withGoogleMap(Map));
const mapBlock = props => (
  <div
    onWheel={() => props.setFitBoundFlag(false)}
    onDoubleClick={() => props.setFitBoundFlag(false)}
    onMouseDown={() => props.setFitBoundFlag(false)}
  >
    <WrappedMap
      googleMapURL={`https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places&key=${process.env.REACT_APP_GOOGLE_KEY}&region=MA`}
      loadingElement={<div style={{ height: `100%` }} />}
      containerElement={<div style={{ height: `900px` }} />}
      mapElement={<div style={{ height: `100%` }} />}
      gabsInfo={props.gabsInfo}
      branchesInfo={props.branchesInfo}
      branchFilteredLocations={props.branchFilteredLocations}
      gabFilteredLocations={props.gabFilteredLocations}
      activeTab={props.activeTab}
      hoveredMark={props.hoveredMark}
      selectedMark={props.selectedMark}
      setHoveredMark={mark => props.setHoveredMark(mark)}
      setSelectedMark={mark => props.setSelectedMark(mark)}
      setFitBoundFlag={value => props.setFitBoundFlag(value)}
      setMapZoom={value => props.setMapZoom(value)}
      fitBoundFlag={props.fitBoundFlag}
      mapZoom={props.mapZoom}
    />
  </div>
);
const mapDispatchToProps = dispatch => ({
  setSelectedMark: mark => {
    dispatch(setSelectedMark(mark));
  },
  setHoveredMark: mark => {
    dispatch(setHoveredMark(mark));
  },
  setFitBoundFlag: value => {
    dispatch(setFitBoundFlag(value));
  },
  setMapZoom: value => {
    dispatch(setMapZoom(value));
  },
});
const mapStateToProps = state => ({
  gabsInfo: state.GeoLocalisations.gabsInfo,
  branchesInfo: state.GeoLocalisations.branchesInfo,
  branchFilteredLocations: state.GeoLocalisations.branchFilteredLocations,
  gabFilteredLocations: state.GeoLocalisations.gabFilteredLocations,
  activeTab: state.GeoLocalisations.activeTab,
  selectedMark: state.GeoLocalisations.selectedMark,
  hoveredMark: state.GeoLocalisations.hoveredMark,
  fitBoundFlag: state.GeoLocalisations.fitBoundFlag,
  mapZoom: state.GeoLocalisations.mapZoom,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(mapBlock),
);
