import React from 'react';
import { Dropdown } from 'primereact/dropdown';
import { Formik } from 'formik';
import { connect } from 'react-redux';
import { withTranslation } from 'react-i18next';
import {
  setGabFiltredLocations,
  setSelectedMark,
  setHoveredMark,
  setFitBoundFlag,
  setMapZoom,
} from '../../../redux/geoLocalisation/localisations.actions';
import ResetIcon from '../../../assets/SvgComponents/ResetSvg/resetSvg';
import { t } from '../../../i18n';
import { GEOLOCALISATION_NAMESPACE } from '../constants';

const gabTab = props => {
  let data = [];
  return (
    <Formik
      initialValues={{
        chosenSubsidiary: '',
      }}
      onSubmit={(values, { setSubmitting }) => {
        setSubmitting(false);
      }}
    >
      {({ values, setFieldValue }) => {
        data = props.gabsInfo.filter(
          branch => branch.bank === values.chosenSubsidiary,
        );

        props.setGabFiltredLocations(data);
        return (
          <div className="tab-pane cont active">
            <form>
              <div className="form-group">
                <label for="">{t(`${GEOLOCALISATION_NAMESPACE}bank`)}</label>
                <Dropdown
                  name="chosenSubsidiary"
                  options={props.options}
                  value={values.chosenSubsidiary}
                  onChange={e => {
                    setFieldValue('chosenSubsidiary', e.value);
                    props.setSelectedMark(null);
                    props.setFitBoundFlag(true);
                    props.setMapZoom(10);
                  }}
                  placeholder={t(`${GEOLOCALISATION_NAMESPACE}chooseBank`)}
                />
              </div>
              <div
                onClick={() => {
                  setFieldValue('chosenSubsidiary', '');
                  props.setSelectedMark(null);
                  props.setFitBoundFlag(true);
                  props.setMapZoom(10);
                }}
                className="form-group effacer-recherche"
              >
                <div className="icon">
                  <ResetIcon />
                </div>
                <span> {t(`${GEOLOCALISATION_NAMESPACE}reset`)}</span>
              </div>
            </form>
            <div className="list-agences">
              <h3>{t(`${GEOLOCALISATION_NAMESPACE}gabList`)}</h3>
              <div className="scrollbar">
                <div className="list-content">
                  {props.gabsInfo.map(gab => {
                    let ref;
                    const handleClick = () =>
                      ref.current.scrollIntoView({
                        behavior: 'smooth',
                        block: 'end',
                        inline: 'start',
                      });
                    if (gab.bank === values.chosenSubsidiary) {
                      ref = React.createRef();
                      return (
                        <div
                          key={gab.gabId}
                          ref={ref}
                          className={
                            props.selectedMark === gab
                              ? 'list-el active-item'
                              : 'list-el'
                          }
                          onClick={() => {
                            handleClick();
                            props.setSelectedMark(gab);
                            props.setFitBoundFlag(false);
                            props.setMapZoom(19);
                          }}
                          onMouseOver={() => {
                            props.setHoveredMark(gab.gabId);
                            props.setFitBoundFlag(false);
                          }}
                          onFocus={() => {
                            props.setHoveredMark(gab.gabId);
                            props.setFitBoundFlag(false);
                          }}
                          onMouseOut={() => {
                            props.setHoveredMark(null);
                            props.setFitBoundFlag(false);
                          }}
                          onBlur={() => {
                            props.setHoveredMark(null);
                            props.setFitBoundFlag(false);
                          }}
                        >
                          <div className="name">
                            {t(`${GEOLOCALISATION_NAMESPACE}gab`)} - {gab.name}
                          </div>
                          <div className="adresse">{gab.address} </div>
                          <div className="telephone">{gab.tel}</div>
                          <div className="link">
                            {t(`${GEOLOCALISATION_NAMESPACE}knowMore`)}
                          </div>
                        </div>
                      );
                    }
                  })}
                </div>
              </div>
            </div>
          </div>
        );
      }}
    </Formik>
  );
};
const mapDispatchToProps = dispatch => ({
  setGabFiltredLocations: data => {
    dispatch(setGabFiltredLocations(data));
  },
  setFitBoundFlag: value => {
    dispatch(setFitBoundFlag(value));
  },
  setSelectedMark: mark => {
    dispatch(setSelectedMark(mark));
  },
  setHoveredMark: mark => {
    dispatch(setHoveredMark(mark));
  },
  setMapZoom: value => {
    dispatch(setMapZoom(value));
  },
});
const mapStateToProps = state => ({
  gabsInfo: state.GeoLocalisations.gabsInfo,
  selectedMark: state.GeoLocalisations.selectedMark,
});

export default withTranslation()(
  connect(
    mapStateToProps,
    mapDispatchToProps,
  )(gabTab),
);
