import React from 'react';
import { connect } from 'react-redux';
import MapBlock from './mapBlock';
import MapTable from './mapTable';
import { getGabLocalisations } from '../../../redux/geoLocalisation/localisations.actions';
import { getBranchesLocalisations } from '../../../redux/geoLocalisation/localisations.actions';

class GeoLocalisation extends React.Component {
  componentDidMount() {
    this.props.getGabLocalisations();
    this.props.getBranchesLocalisations();
  }

  render() {
    return (
      <div className="bcp-content-home">
        <section>
          <div className="block-map">
            <MapBlock />
            <MapTable />
          </div>
        </section>
      </div>
    );
  }
}
const mapDispatchToProps = dispatch => ({
  getGabLocalisations: () => {
    dispatch(getGabLocalisations());
  },
  getBranchesLocalisations: () => {
    dispatch(getBranchesLocalisations());
  },
});

export default connect(
  null,
  mapDispatchToProps,
)(GeoLocalisation);
